<template>
  <div class="order-management">
    <h1>Order Management</h1>
    <form @submit.prevent="submitOrder">
      <label for="orderDate">Order Date:</label>
      <input type="date" v-model="orderForm.orderDate" required />

      <label for="orderStatus">Order Status:</label>
      <select v-model="orderForm.orderStatus" required>
        <option value="PENDING">Pending</option>
        <option value="SHIPPED">Shipped</option>
        <option value="DELIVERED">Delivered</option>
        <option value="CANCELLED">Cancelled</option>
      </select>

      <label for="paymentMethod">Payment Method:</label>
      <select v-model="orderForm.paymentMethod" required>
        <option value="CREDIT_CARD">Credit Card</option>
        <option value="DEBIT_CARD">Debit Card</option>
        <option value="PAYPAL">PayPal</option>
        <option value="CASH_ON_DELIVERY">Cash on Delivery</option>
      </select>

      <label for="shipping">Shipping:</label>
      <input type="text" v-model="orderForm.shippingOrCollected" required />

      <label for="totalAmount">Total Amount:</label>
      <input type="number" v-model="orderForm.totalAmount" required />

      <label for="cartID">Cart ID:</label>
      <input type="number" v-model="orderForm.cartID" required />

      <label for="shippingID">Shipping ID:</label>
      <input type="number" v-model="orderForm.shippingID" required />

      <button type="submit">Submit</button>
    </form>

    <h2>Orders</h2>
    <table>
      <thead>
      <tr>
        <th>Order ID</th>
        <th>Order Date</th>
        <th>Order Status</th>
        <th>Payment Method</th>
        <th>shippingOrCollected</th>
        <th>Total Amount</th>
        <th>Cart ID</th>
        <th>Shipping ID</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="order in orders" :key="order.id">
        <td>{{ order.id }}</td>
        <td>{{ order.orderDate }}</td>
        <td>{{ order.orderStatus }}</td>
        <td>{{ order.paymentMethod }}</td>
        <td>{{ order.shippingOrCollected }}</td>
        <td>{{ order.totalAmount }}</td>
        <td>{{ order.cartID }}</td>
        <td>{{ order.shippingID }}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { getOrders, createOrder } from '@/services/orderService';

export default {
  data() {
    return {
      orders: [],
      orderForm: {
        orderDate: '',
        orderStatus: '',
        paymentMethod: '',
        shippingOrCollected: '',
        totalAmount: '',
        cartID: '',
        shippingID: ''
      }
    };
  },
  methods: {
    fetchOrders() {
      getOrders()
          .then(data => {
            this.orders = data;
          })
          .catch(error => {
            console.error('Error fetching orders:', error);
          });
    },
    submitOrder() {
      const order = { ...this.orderForm };
      createOrder(order)
          .then(() => {
            this.fetchOrders();
            this.resetForm();
          })
          .catch(error => {
            console.error('Error creating order:', error);
          });
    },
    resetForm() {
      this.orderForm = {
        orderDate: '',
        orderStatus: '',
        paymentMethod: '',
        shippingOrCollected: '',
        totalAmount: '',
        cartID: '',
        shippingID: ''
      };
    }
  },
  mounted() {
    this.fetchOrders();
  }
};
</script>

<style scoped>
.order-management {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  text-align: left;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

button {
  padding: 5px 10px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #2980b9;
}
</style>